* {
    margin: 0;
    padding: 0;
}

.video-background {
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
}

header {
    height: 10vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background: rgb(22, 22, 22);
    width: 100%;
    position: absolute;
}

header img {
    margin: 0 1% 0 10%;
    height: 8vh;
    background-size: cover;
}

.nav-header {
    margin: 0 10% 0 1%;
    display: flex;
    flex-direction: row;
    text-decoration: none;
}

.link {
    text-decoration: none;
}

.nav-header li {
    font-family: lobster, Arial, sans-serif;
    font-size: 24px;
    color: rgba(30,144,255, 1);
    list-style: none;
    margin-left: 18px;
    font-weight: bolder;
    cursor: pointer;
}

main {
    position: relative;
    top: 10vh;
    color: #ffffff;
}

.content-main {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 90vh;
    margin: 0px 150px 0px 150px;
}